
import { defineComponent, ref, computed, onMounted } from "vue";

import { useRouter } from "vue-router";

import Navi from "../components/Navi.vue";
import ShopbagItem from "../components/ShopbagItem.vue";

import axios from "axios";

import { Toast } from "vant";

export default defineComponent({
  name: "Shopbag",
  components: {
    Navi,
    ShopbagItem,
  },
  setup() {
    let router: any = useRouter();

    //是否处于编辑状态
    let isEdit: any = ref<boolean>(false);

    let text: any = ref<string>("编辑");

    let isAllSelect: any = ref<boolean>(false);

    //购物袋数据
    let shopbagData: any = ref<Array<any>>([]);

    let isLogin: any = ref<boolean>(true);

    //计算订单总价
   const total = computed(() => {
      let total: number = 0;
      shopbagData.value.forEach((item: any): void => {
        if (item.isCheck) {
          total += item.price * item.count;
        }
      });

      return total * 100;
    });

    //编辑
    let edit: () => void = (): void => {
      isEdit.value = !isEdit.value;
      text.value = isEdit.value ? "完成" : "编辑";
    };

    //去登录
    let goLogin: () => void = (): void => {
      router.push({ name: "Login" });
    };

    //获取购物袋商品
    let getShopbagProduct: () => void = (): void => {
      axios({
        method: "get",
        url: "/findAllShopcart",
      })
        .then((result: any): void => {
          if (result.data.code === 700) {
            isLogin.value = false;
            text.value = "";
            return;
          }

          if (result.data.code === 5000) {
            result.data.result.forEach((item: any): void => {
              item.isCheck = false;
            });
            shopbagData.value = result.data.result;

            if (shopbagData.value.length === 0) {
              text.value = "";
            }
          }
        })
        .catch((err: any): void => {
          
        });
    };

    //全选
    let allSelect: () => void = (): void => {
      shopbagData.value.forEach((item: any): void => {
        item.isCheck = isAllSelect.value;
      });
    };

    //单选
    let selectOne: () => void = (): void => {
      for (let i = 0; i < shopbagData.value.length; i++) {
        if (!shopbagData.value[i].isCheck) {
          isAllSelect.value = false;
          return;
        }
      }

      isAllSelect.value = true;
    };

    //修改数量
    let updateCount: (sid: string, count: number) => void = (
      sid: string,
      count: number
    ): void => {
      axios({
        method: "post",
        url: "/modifyShopcartCount",
        data: {
          sid,
          count,
        },
      })
        .then((result: any): void => {
          if (result.data.code === 700) {
            Toast(result.data.msg);
            router.push({ name: "Login" });
            return;
          }
        })
        .catch((err: any): void => {
          
        });
    };

    //删除商品
    let removeShopbag: (sids: Array<string>) => void = (
      sids: Array<string>
    ): void => {
      //sids: 购物袋的sid, array

      axios({
        method: "post",
        url: "/deleteShopcart",
        data: {
          sids: JSON.stringify(sids),
        },
      })
        .then((result: any): void => {
          Toast(result.data.msg);

          if (result.data.code === 700) {
            router.push({ name: "Login" });
            return;
          }

          if (result.data.code === 7000) {
            sids.forEach((sid) => {
              for (let i = shopbagData.value.length - 1; i >= 0; i--) {
                if (sid === shopbagData.value[i].sid) {
                  shopbagData.value.splice(i, 1);
                  break;
                }
              }
            });

            selectOne();

            if (shopbagData.value.length === 0) {
              text.value = "";
            }
          }
        })
        .catch((err: any): void => {
          
        });
    };

    //删除选择的商品
    let removeSelectShopbag: () => void = (): void => {
      //获取删除的购物袋数据的sid
      let sids: Array<string> = [];

      shopbagData.value.forEach((item: any): void => {
        if (item.isCheck) {
          sids.push(item.sid);
        }
      });

      removeShopbag(sids);
    };

    //提交订单
    let commitOrder: () => void = (): void => {
      let sids: Array<string> = [];
      shopbagData.value.forEach((item: any): void => {
        if (item.isCheck) {
          sids.push(item.sid);
        }
      });

      if (sids.length === 0) {
        Toast("请选择下单的商品");
        return;
      }
      router.push({ name: "OrderBuy", query: { sids: sids.join("-") } });
    };

    onMounted(() => {
      getShopbagProduct();
    })

    return {
      isEdit,
      text,
      isAllSelect,
      shopbagData,
      isLogin,
      total,
      edit,
      goLogin,
      getShopbagProduct,
      allSelect,
      selectOne,
      updateCount,
      removeShopbag,
      removeSelectShopbag,
      commitOrder
    };
  },
});
