
import { defineComponent } from 'vue'

  export default defineComponent({
    name: 'ShopbagItem',
    props: {
      shopbagItemData: {
        type: Object,
        default() {
          return {};
        }
      }
    },

    setup(props, {emit}) {

      let selectOne: () => void = (): void => {
        emit('select-one');
      }

      let updateCount: () => void = (): void => {
        emit('update-count');
      }

      return {
        selectOne,
        updateCount
      }
    }
  })
